import { ActionFunction, json } from '@remix-run/node';
import { useActionData } from '@remix-run/react';
import { useMemo } from 'react';
import Web3 from 'web3';

export const action: ActionFunction = async ({ request }) => {
    const form = await request.formData();

    const rpcUrl = form.get('rpc-url');
    const privateKey = form.get('private-key');
    const contractAbi = form.get('contract-abi');
    const contractAddress = form.get('contract-address');

    return json({ rpcUrl, contractAbi, contractAddress, privateKey });
};

